@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Raleway:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
}
body, html {
/* font-family: 'Montserrat', sans-serif; */
 font-family: 'Open Sans', sans-serif;
    font-size:16px;
    height: 100%;
    margin: 0;
    color:#424242;
}
.hidden {display: none}

h2, h3, h4, h5, h6 {
    color: #0e0d0d;
}

h2 {
    letter-spacing: 0.5px;
    line-height: 2rem;
}

.grey-bg {background: #F2F3F4}

