@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Raleway:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
}
body, html {
/* font-family: 'Montserrat', sans-serif; */
 font-family: 'Open Sans', sans-serif;
    font-size:16px;
    height: 100%;
    margin: 0;
    color:#424242;
}
.hidden {display: none}

h2, h3, h4, h5, h6 {
    color: #0e0d0d;
}

h2 {
    letter-spacing: 0.5px;
    line-height: 2rem;
}

.grey-bg {background: #F2F3F4}


main {
    width: 100%;
}
.container {
    width: 100%;
    max-width: 1080px;
    margin-left:auto;
    margin-right:auto;
    padding-left: 10px;
    padding-right: 10px;
}


